import React from 'react';
import './Expertise.module.css';
import laptop from './laptop.png';

const Expertise = () => {
    return (
        <div class="expertise">
            <div class="container">
                <div class="row">
                    <div class="col-md-6 col-xs-12 col-md-offset-6">
                        <div class="row">
                            <div class="col-md-12">
                                <h2 class="block-title">  Drupal,<br />  14 !</h2>
                            </div>
                        </div>
                        <div class="row row-flex expertise-row">
                            <div class="col-sm-6 col-xs-12 expertise-col">
                                <div class="expertise-item">
                                    <div class="expertise-item-body">
                                             ,   !
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-6 col-xs-12 expertise-col">
                                <div class="expertise-item">
                                    <div class="expertise-item-body">
                                         Drupal ,        CMS!
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-6 col-xs-12 expertise-col">
                                <div class="expertise-item">
                                    <div class="expertise-item-body">
                                            Drupal    Drupal.org,  <a href="/drupalgive">  Drupal</a>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-6 col-xs-12 expertise-col">
                                <div class="expertise-item">
                                    <div class="expertise-item-body">
                                           Drupal 5, 6, 7  8
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="expertise-img"><img alt="Helpdesk" class="img-responsive" src={laptop} /></div>
        </div>
    );
}

export default Expertise;